$color-black: #000000;
$color-white: #ffffff;

a {
    text-decoration: none;
    color: $color-white;
}

body {

    background-image: linear-gradient(to top, rgba($color-black, 0.9), rgba($color-black, 0.9)), url('/img/developer.jpg');
    background-size: cover;
    background-repeat: no-repeat;

    @media screen and (max-width: 991px) {
        font-size: 10px;
    }


    .hero {
        height: 100vh;
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        overflow: hidden;
        color: $color-white;

        &__title {
            font-size: 8vw;
        }
    }

    .menu {
        
        display: flex;
        justify-content: space-between;
        opacity: 0;
        position: fixed;
        bottom: 0;
        width: 100%;
        text-transform: uppercase;
        letter-spacing: 0.25em;

        &__button {
            flex: 1 1 25%;
            padding: 10px 15px;
            border: 1px solid $color-white;
            text-align: center;
            display: flex;
            justify-content: center;
            align-items: center;
            background: #222222;
            color: $color-white;
            cursor: pointer;

            &.active {
                background: $color-black;
            }
        }
    }

    .resume {
        height: 100vh;
        display: flex;
        overflow: hidden;
        color: $color-white;

        &__slider {
            margin: auto;
            width: 100%;
            
            .slick-slide {
                transform: scale(0.8);
                transition: transform 1s ease;
                
                @media screen and (max-width: 991px) {
                    transform: scale(0.9);
                }
                
                & > div {
                    display: flex;
                }
            }
    
            .slick-center {
                transform: scale(1.0);

                .resume__slider__entry {
                    &::after {
                        opacity: 0;
                    }
                }
            }
    
            .slick-list {
                transform: translate(100%);
                transition: transform 1s ease;
    
                .slick-track {
                    display: flex;
                    align-items: center;
                }
            }
    
            &__entry {
                padding: 15px;
                background: $color-black;

                &__container {
                    background: rgba($color-white, 0.9);
                    color: $color-black;
                    padding: 15px;
                }

                &__list {
                    padding-inline-start: 20px;
                }
    
                @media screen and (max-width: 991px) {
                    padding: 10px;
                }
    
                &:focus {
                    outline: none !important;
                }

                &__line {
                    display: flex;
                    justify-content: space-between;
                    flex-wrap: wrap;
                    &__position, &__location, &__tech {
                        font-style: italic;
                    }
                }

                &__link {
        
                    display: flex;
                    align-items: center;
        
                    .icon {
                        height: 15px;
                    }
                }

                &__list {
                    &:last-child {
                        margin-bottom: 0.45em;
                    }
                }

                &::after {
                    content: '';
                    position: absolute;
                    width: 100%;
                    height: 100%;
                    left: 0;
                    top: 0;
                    background: $color-black;
                    opacity: 0.8;
                    transition: opacity 1s ease;
                }
            }
        }
    }

    .portfolio {
        height: 100vh;
        display: flex;
        overflow: hidden;
        &__slider {

            margin: auto;
            width: 100%;
            
            .slick-slide {
                transform: scale(0.8);
                transition: transform 1s ease;
                
                @media screen and (max-width: 991px) {
                    transform: scale(0.9);
                }
                
                & > div {
                    display: flex;
                }
            }

            .slick-center {
                transform: scale(1.0);

                .portfolio__slider__vid {
                    &::after {
                        opacity: 0;
                    }
                }
            }

            .slick-list {
                transform: translate(100%);
                transition: transform 1s ease;

                .slick-track {
                    display: flex;
                    align-items: center;
                }
            }

            &__vid {
                padding: 15px;
                background: black;

                &::after {
                    content: '';
                    position: absolute;
                    width: 100%;
                    height: 100%;
                    left: 0;
                    top: 0;
                    background: $color-black;
                    opacity: 0.8;
                    transition: opacity 1s ease;
                }

                @media screen and (max-width: 991px) {
                    padding: 10px;
                }

                &:focus {
                    outline: none !important;
                }

                video {
                    width: 100%;

                }
            }
        }
    }
}